import searchResultArticle from '../../components/searchResultArticle.vue';
export default {
	data() {
		return {
			hotList: [],
			searchKeyWords: '',
			searchedArticleList: [],
			searching: true,
			searchHisKeyList: uni.getStorageSync('search_history'),

			userInfo: this.getGlobalUserInfo(),

			totalPage: 1,
			currentPage: 1,
		}
	},
	components: {
		searchResultArticle,
	},
	created() {
		// 查询热搜词
		this.getHotWords();
	},
	// onReachBottom() {
	// 	this.loadMore();
	// },
	methods: {
		getHotWords: function() {
			console.log('dasdsdad');

			var that = this;
			uni.request({
				url: that.$serverUrl + '/article/hot',
				method: "POST",
				success: (res) => {
					console.log(res);
					that.hotList = res.data.data;
					console.log(that.hotList);
				}

			})
		},
		search: function(page) {
			var that = this;
			var isSaveRecord = 1;
			// console.log(page);
			if (this.searchKeyWords == '' || this.searchKeyWords == null) {
				uni.showToast({
					title: '搜索内容不能为空',
					duration: 1000,
					icon: 'none',
				})
				return;
			}

			uni.getStorage({
				key: 'search_history',
				success(res) {
					let list = res.data;
					// console.log(list);
					if (list.length > 10) {
						for (let item of list) {
							if (item == that.searchKeyWords) {
								return false;
							}
						}
						list.pop();
						list.unshift(that.searchKeyWords);
					} else {
						for (let item of list) {
							if (item == that.searchKeyWords) {
								return false;
							}
						}
						list.unshift(that.searchKeyWords);
					}
					that.searchHisKeyList = list;
					uni.setStorage({
						key: 'search_history',
						data: that.searchHisKeyList
					})
				},
				fail() {
					that.searchHisKeyList = [];
					that.searchHisKeyList.push(that.searchKeyWords);
					uni.setStorage({
						key: 'search_history',
						data: that.searchHisKeyList
					});
				}
			});

			uni.showLoading({
				title: "搜索中..."
			})

			uni.request({
				url: that.$serverUrl + '/article/searchArticleYANG',
				method: "POST",
				data: {
					articleContent: that.searchKeyWords,
					userId: that.userInfo.id,
					isSaveRecord: isSaveRecord,
					page: page,
				},
				success: function(result) {
					uni.hideLoading();
					console.log(result);
					// console.log(result.data);
					// that.searchedArticleList = result.data.data.rows;
					
					that.searching = false;
					if(result.data.status == 200){
						// 判断当前页是不是第一页，如果是第一页，那么设置showList为空
						if (that.currentPage == 1) {
							that.searchedArticleList = [];
						}
						
						var newArticleList = result.data.data.rows;
						var oldArticleList = that.searchedArticleList;
						that.searchedArticleList = oldArticleList.concat(newArticleList);
						// console.log(result.data.data.page);
						that.currentPage = page;
						that.totalPage = result.data.data.total;
					}
					
				},
				fail: (res) => {
					console.log("index unirequest fail");
					console.log(res);
				}
			})
		},
		searchCancle: function(searching){
				this.searching = !searching;
				console.log(this.searching);
		},
		loadMore: function() {
			var that = this;
			var currentPage = that.currentPage;
			console.log('currentpage is ' + currentPage);
			var totalPage = that.totalPage;
			console.log('totalpage is ' + totalPage);
			// 判断当前页数和总页数是否相等
			if (currentPage == totalPage) {
				// that.showArticles(1);
				uni.showToast({
					title: "没有更多文章了",
					icon: "none",
					duration: 1000
				})
			} else {
				var page = currentPage + 1;
				that.search(page);
			}
		},
		searchDeleteAll: function() {
			var that = this;
			uni.showModal({
				title: "提示",
				content: '确定删除所有历史记录吗?',
				success: function(res) {
					if (res.confirm) {
						that.searchHisKeyList = [];
						uni.setStorage({
							key: 'search_history',
							data: that.searchHisKeyList,
						})
					} else if (res.cancle) {

					}
				}
			})
		},
		searchCancle: function(searching) {
			this.searching = !searching;
			// console.log(this.searching);
		},
		exitSearch() {
			this.hotList = "",
				this.searchKeyWords = "",
				this.searchedArticleList = "",
				this.$emit("exitSearchSignal", 0)
		},
		putHotIntoInput: function(index){
			// console.log(index);
			var keywords = this.hotList[index];
			// console.log(keywords);
			this.searchKeyWords = keywords;
		},
		putHisIntoInput: function(index){
			// console.log(index);
			var keywords = this.searchHisKeyList[index];
			// console.log(keywords);
			this.searchKeyWords = keywords;
		}
	}
}
